import React, { createContext, useState, useContext, useEffect } from 'react';

const AuthContext = createContext(null);

export const AuthProvider = ({ children }) => {
  const [token, setToken] = useState(localStorage.getItem('token'));
  const [isAuthenticated, setIsAuthenticated] = useState(!!token);
  const [userType, setUserType] = useState(localStorage.getItem('userType') || null);
  const [userData, setUserData] = useState(JSON.parse(localStorage.getItem('userData')) || null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (token) {
      localStorage.setItem('token', token);
      setIsAuthenticated(true);
    } else {
      localStorage.removeItem('token');
      localStorage.removeItem('userType');
      localStorage.removeItem('userData');
      setIsAuthenticated(false);
      setUserType(null);
      setUserData(null);
    }
  }, [token]);

  useEffect(() => {
    if (userType) {
      localStorage.setItem('userType', userType);
    }
  }, [userType]);

  useEffect(() => {
    if (userData) {
      localStorage.setItem('userData', JSON.stringify(userData));
    }
  }, [userData]);

  // Load user profile when authenticated
  useEffect(() => {
    const fetchUserProfile = async () => {
      if (isAuthenticated && userType === 'user' && token) {
        try {
          const response = await fetch('http://localhost:8000/users/me', {
            headers: {
              'Authorization': `Bearer ${token}`
            }
          });
          
          if (response.ok) {
            const data = await response.json();
            setUserData(data);
          }
        } catch (error) {
          console.error('Error fetching user profile:', error);
        }
      }
    };

    fetchUserProfile();
  }, [isAuthenticated, userType, token]);

  const login = async (usernameOrEmail, password) => {
    setIsLoading(true);
    try {
      const formData = new FormData();
      formData.append('username', usernameOrEmail);
      formData.append('password', password);

      const response = await fetch('http://localhost:8000/token', {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        throw new Error('Login failed');
      }

      const data = await response.json();
      if (data.access_token) {
        setToken(data.access_token);
        setUserType(data.user_type);
        
        // If user, fetch profile data
        if (data.user_type === 'user') {
          const profileResponse = await fetch('http://localhost:8000/users/me', {
            headers: {
              'Authorization': `Bearer ${data.access_token}`
            }
          });
          
          if (profileResponse.ok) {
            const profileData = await profileResponse.json();
            setUserData(profileData);
          }
        } else {
          // For admin, just store basic info
          setUserData({ username: data.username });
        }
        
        setIsAuthenticated(true);
        return true;
      } else {
        throw new Error('No access token received');
      }
    } catch (error) {
      console.error('Login error:', error);
      setIsAuthenticated(false);
      setToken(null);
      setUserType(null);
      setUserData(null);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  const register = async (email, username, password, fullName) => {
    setIsLoading(true);
    try {
      const response = await fetch('http://localhost:8000/users/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          email,
          username,
          password,
          full_name: fullName,
        }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.detail || 'Registration failed');
      }

      const userData = await response.json();
      
      // Automatically log in after successful registration
      return await login(username, password);
    } catch (error) {
      console.error('Registration error:', error);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  const updateProfile = async (updateData) => {
    if (!isAuthenticated || !token) {
      throw new Error('Not authenticated');
    }

    try {
      const response = await fetch('http://localhost:8000/users/me', {
        method: 'PUT',
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(updateData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.detail || 'Profile update failed');
      }

      const updatedUserData = await response.json();
      setUserData(updatedUserData);
      return updatedUserData;
    } catch (error) {
      console.error('Profile update error:', error);
      throw error;
    }
  };

  const saveEvent = async (eventId) => {
    if (!isAuthenticated || !token || userType !== 'user') {
      throw new Error('Not authenticated as user');
    }

    try {
      const response = await fetch(`http://localhost:8000/users/me/save-event/${eventId}`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        throw new Error('Failed to save event');
      }

      // Refresh user data to get updated saved events
      const profileResponse = await fetch('http://localhost:8000/users/me', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (profileResponse.ok) {
        const profileData = await profileResponse.json();
        setUserData(profileData);
      }

      return true;
    } catch (error) {
      console.error('Save event error:', error);
      throw error;
    }
  };

  const saveOpportunity = async (opportunityId) => {
    if (!isAuthenticated || !token || userType !== 'user') {
      throw new Error('Not authenticated as user');
    }

    try {
      const response = await fetch(`http://localhost:8000/users/me/save-opportunity/${opportunityId}`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        throw new Error('Failed to save opportunity');
      }

      // Refresh user data to get updated saved opportunities
      const profileResponse = await fetch('http://localhost:8000/users/me', {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (profileResponse.ok) {
        const profileData = await profileResponse.json();
        setUserData(profileData);
      }

      return true;
    } catch (error) {
      console.error('Save opportunity error:', error);
      throw error;
    }
  };

  const logout = () => {
    setToken(null);
    setIsAuthenticated(false);
    setUserType(null);
    setUserData(null);
  };

  // Calculate isAdmin and isUser properties
  const isAdmin = userType === 'admin';
  const isUser = userType === 'user';

  return (
    <AuthContext.Provider
      value={{
        token,
        isAuthenticated,
        userType,
        userData,
        isLoading,
        isAdmin,
        isUser,
        login,
        logout,
        register,
        updateProfile,
        saveEvent,
        saveOpportunity,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
}; 